import jss from 'jss';
import preset from 'jss-preset-default';
import stylesheet from './stylesheet';
export function appendStyleSheet(config) {
    jss.setup(preset());
    const sheet = jss.createStyleSheet(stylesheet(config));
    sheet.attach();
    return sheet;
}
